/* ============================ */
/* ðŸŒ— DARK/LIGHT THEME VARIABLEN */
/* ============================ */

:root {
  --color-bg: #ffffff;
  --color-text: #1f1f1f;
  --color-card: #f7f9fb;
  --color-border: #dae2ec;
  --color-button: #2d85f0;
  --color-button-hover: #1c6fd1;
  --color-form: #f2f4f7;
  --color-input-bg: #ffffff;
  --color-input-border: #ccc;
  --color-modal: #ffffff;
  --color-overlay: rgba(0, 0, 0, 0.4);
}

[data-theme="dark"] {
  --color-bg: #1e1e2f;
  --color-text: #e0e0e0;
  --color-card: #2f3445;
  --color-border: #3e445a;
  --color-button: #3e8ef7;
  --color-button-hover: #2e76d7;
  --color-form: #2c303f;
  --color-input-bg: #1f202c;
  --color-input-border: #555;
  --color-modal: #2a2f3c;
  --color-overlay: rgba(10, 10, 10, 0.6);
}

body {
  font-family: 'Segoe UI', sans-serif;
  background: var(--color-bg);
  color: var(--color-text);
  margin: 0;
  padding: 2rem;
  transition: background 0.3s, color 0.3s;
}

.app {
  max-width: 900px;
  margin: 0 auto;
  background: var(--color-card);
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.06);
}

h1 {
  text-align: center;
  font-size: 2.2rem;
  color: var(--color-text);
  margin-bottom: 1.5rem;
}

/* GerÃ¤tekarte */
.device-card {
  display: flex;
  align-items: center;
  gap: 1rem;
  background: var(--color-card);
  padding: 1rem;
  border-radius: 10px;
  margin-bottom: 1rem;
  border: 1px solid var(--color-border);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
}

.device-card img {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: 6px;
  border: 1px solid var(--color-input-border);
}

/* Buttons */
button {
  padding: 0.4rem 1rem;
  border: none;
  border-radius: 6px;
  background: var(--color-button);
  color: #fff;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s;
}

button:hover {
  background: var(--color-button-hover);
}

.import-btn {
  background-color: #10b981;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  color: white;
  cursor: pointer;
  margin-left: 10px;
}

.import-btn:hover {
  background-color: #0f9e6e;
}

/* Formular */
.form {
  background: var(--color-form);
  padding: 1rem;
  border-radius: 12px;
  margin-bottom: 2rem;
}

.form input,
.form select {
  width: 100%;
  padding: 0.6rem;
  margin: 0.5rem 0;
  font-size: 1rem;
  border: 1px solid var(--color-input-border);
  border-radius: 6px;
  background: var(--color-input-bg);
  color: var(--color-text);
}

.form input::placeholder {
  color: #aaa;
}

/* Controller-Reihe */
.controller-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 0.5rem;
  align-items: center;
}

/* GerÃ¤teaktionen */
.device-actions {
  display: flex;
  gap: 1rem;
  align-items: center;
  margin-bottom: 1.5rem;
}

/* Modal */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: var(--color-overlay);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

.modal {
  background: var(--color-modal);
  padding: 2rem;
  border-radius: 12px;
  max-width: 500px;
  width: 90%;
  box-shadow: 0 20px 40px rgba(0,0,0,0.5);
  z-index: 11;
}
